@use 'src/styles/mixins';

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html, body {
  margin: 0;
  padding: 0;
}

button {
  border: 0;
  padding: 0;
}
body {
  font-family: mixins.$manrope;
  font-weight: 400;
}
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
  margin: 0;
}
